.product{
      /* border: 2px solid red; */
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      margin: 10px;
      padding: 20px;
      width: 100%;
      min-height: 400px;
      min-width: 100px;
      background-color: white;
      position: relative;
      bottom: 80px;
      z-index: 1;
}

.product > img{
      max-height: 200px;
      width: 100%;
      object-fit: contain;
      margin-bottom: 15px;
}

.product > button{
      background: #f0c14b;
      border: 1px solid;
      margin-top: 10px;
      border-color: #a88734 #9c7e31 #846a29;
      color: #111;
      font-weight: bold;
}

.product_price{
      margin-top: 5px;
}

.product_info{
      /* border: 2px solid red; */
      height: 100px;
      margin-bottom: 15px;
}

.product_rating{
      display: flex;
}